<h3 id="教程" tabindex="-1"><a class="header-anchor" href="#教程" aria-hidden="true">#</a> 教程</h3>
<p><a href="https://www.cnblogs.com/phyger/p/14035937.html#ghpages_681" target="_blank" rel="noopener noreferrer">打造完美写作系统：Gitbook+Github Pages+Github Actions<OutboundLink/></a></p>
<p>用上面的方法完成githubPages的搭建，github Actions使用没成功，Windows写批处理程序实现同样效果</p>
<ol>
<li>
<p>更新完笔记，重构gitbook(0-gitbook_build.bat)</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token builtin class-name">cd</span> gitbook
gitbook build
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br></div></div></li>
<li>
<p>上传git(1-git_updata.bat)</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token builtin class-name">cd</span> gitbook
<span class="token function">git</span> <span class="token function">add</span> <span class="token builtin class-name">.</span>
<span class="token function">git</span> commit -m <span class="token string">"updata"</span>
<span class="token function">git</span> push https://github.com/用户名/仓库名
<span class="token builtin class-name">cd</span> _book
<span class="token function">git</span> config --global user.email <span class="token string">"邮箱"</span>
<span class="token function">git</span> config --global user.name <span class="token string">"用户名"</span>
<span class="token function">git</span> init
<span class="token function">git</span> remote <span class="token function">add</span> origin https://github.com/用户名/仓库名
<span class="token function">git</span> <span class="token function">add</span> <span class="token builtin class-name">.</span>
<span class="token function">git</span> commit -m <span class="token string">"updata"</span>
<span class="token function">git</span> branch -M main
<span class="token function">git</span> push --quiet --force <span class="token string">"https://github.com/用户名/仓库名"</span> main:gh-pages <span class="token comment">#没有成功在下一行加上"pause"</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br></div></div><p>（<strong>重点</strong>）目录结构：<br>
<img src="https://cdn.jsdelivr.net/gh/oyw6719119/gitbookpig/202110041151795.png" alt="image-20211004115111743"></p>
<p>git上传常见错误：</p>
<ol>
<li><a href="https://www.cnblogs.com/fairylyl/p/15059437.html" target="_blank" rel="noopener noreferrer">Git 错误：OpenSSL SSL_read: Connection was reset, errno 10054<OutboundLink/></a></li>
</ol>
</li>
</ol>
<p><a href="https://blog.csdn.net/wkc1996/article/details/119458835" target="_blank" rel="noopener noreferrer">CDN加速GitHub搭配PicGo自建图床，Typora使用起来太爽了<OutboundLink/></a></p>
<h3 id="插件篇" tabindex="-1"><a class="header-anchor" href="#插件篇" aria-hidden="true">#</a> 插件篇</h3>
<p><a href="https://www.cnblogs.com/mingyue5826/p/10307051.html#214-%E7%94%9F%E6%88%90%E9%A1%B5%E5%86%85%E7%9B%AE%E5%BD%95" target="_blank" rel="noopener noreferrer">插件整理<OutboundLink/></a></p>
<p>插件安装：在gitbook init目录中输入</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>gitbook <span class="token function">install</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>页内目录生成插件<a href="https://blog.csdn.net/xo19882011/article/details/114917307" target="_blank" rel="noopener noreferrer">ref<OutboundLink/></a></p>
<div class="language-json ext-json line-numbers-mode"><pre v-pre class="language-json"><code><span class="token punctuation">{</span>
    <span class="token property">"plugins"</span><span class="token operator">:</span> <span class="token punctuation">[</span>
        <span class="token string">"intopic-toc"</span>
    <span class="token punctuation">]</span><span class="token punctuation">,</span>
<span class="token punctuation">}</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br></div></div><p><img src="https://cdn.jsdelivr.net/gh/oyw6719119/gitbookpig/202110041506497.png" alt="image-20211004150613446"></p>
<p>页脚</p>
<div class="language-json ext-json line-numbers-mode"><pre v-pre class="language-json"><code><span class="token property">"plugins"</span><span class="token operator">:</span> <span class="token punctuation">[</span>
    <span class="token string">"tbfed-pagefooter"</span><span class="token punctuation">,</span>
<span class="token punctuation">]</span><span class="token punctuation">,</span>
<span class="token property">"pluginsConfig"</span><span class="token operator">:</span> <span class="token punctuation">{</span>
    <span class="token property">"tbfed-pagefooter"</span><span class="token operator">:</span> <span class="token punctuation">{</span>
        <span class="token property">"copyright"</span><span class="token operator">:</span> <span class="token string">"Copyright &amp;copy oyw6719119 2021"</span><span class="token punctuation">,</span>
        <span class="token property">"modify_label"</span><span class="token operator">:</span> <span class="token string">"最近修订时间："</span><span class="token punctuation">,</span>
        <span class="token property">"modify_format"</span><span class="token operator">:</span> <span class="token string">"YYYY-MM-DD HH:mm:ss"</span>
    <span class="token punctuation">}</span>
<span class="token punctuation">}</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br></div></div><p><img src="https://cdn.jsdelivr.net/gh/oyw6719119/gitbookpig/202110041940031.png" alt="image-20211004194017542"></p>
<p>回到顶部</p>
<div class="language-json ext-json line-numbers-mode"><pre v-pre class="language-json"><code><span class="token property">"plugins"</span><span class="token operator">:</span> <span class="token punctuation">[</span>
    <span class="token string">"back-to-top-button"</span>
<span class="token punctuation">]</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div><p><img src="https://cdn.jsdelivr.net/gh/oyw6719119/gitbookpig/202110041941235.png" alt="image-20211004194154378"></p>
<p>统计网页打开数量 <a href="https://blog.csdn.net/u012887259/article/details/102425733" target="_blank" rel="noopener noreferrer">ref<OutboundLink/></a></p>
<p>没有实际效果</p>
<div class="language-json ext-json line-numbers-mode"><pre v-pre class="language-json"><code><span class="token property">"plugins"</span><span class="token operator">:</span> <span class="token punctuation">[</span>
    <span class="token string">"pageview-count"</span>
<span class="token punctuation">]</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div><p><img src="https://cdn.jsdelivr.net/gh/oyw6719119/gitbookpig/202110051601244.png" alt="image-20211005160051605"></p>
<p>代码块复制按钮 <a href="https://blog.csdn.net/bocai_xiaodaidai/article/details/100696794" target="_blank" rel="noopener noreferrer">ref<OutboundLink/></a></p>
<div class="language-json ext-json line-numbers-mode"><pre v-pre class="language-json"><code><span class="token property">"plugins"</span><span class="token operator">:</span> <span class="token punctuation">[</span>
    <span class="token string">"code"</span>
<span class="token punctuation">]</span><span class="token punctuation">,</span>
<span class="token property">"pluginsConfig"</span><span class="token operator">:</span> <span class="token punctuation">{</span>
    <span class="token property">"code"</span><span class="token operator">:</span> <span class="token punctuation">{</span>
        <span class="token property">"copyButtons"</span><span class="token operator">:</span> <span class="token boolean">false</span>
    <span class="token punctuation">}</span>
<span class="token punctuation">}</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br></div></div><p>copyButtons: false 表示关闭复制按钮</p>
<p><img src="https://cdn.jsdelivr.net/gh/oyw6719119/gitbookpig/202110052300755.png" alt="image-20211005230042701"></p>
<p>自由调节左侧边栏 <a href="https://lingsword.gitbooks.io/gitbook-use/content/plugins.html#splitter" target="_blank" rel="noopener noreferrer">ref<OutboundLink/></a></p>
<div class="language-json ext-json line-numbers-mode"><pre v-pre class="language-json"><code><span class="token property">"plugins"</span><span class="token operator">:</span> <span class="token punctuation">[</span>
    <span class="token string">"splitter"</span>
<span class="token punctuation">]</span><span class="token punctuation">,</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div><p><img src="https://cdn.jsdelivr.net/gh/oyw6719119/gitbookpig/202110052309627.png" alt="image-20211005230908037"></p>
<p>悬浮页内目录（可收起）</p>
<div class="language-json ext-json line-numbers-mode"><pre v-pre class="language-json"><code><span class="token property">"plugins"</span><span class="token operator">:</span> <span class="token punctuation">[</span>
	<span class="token string">"anchor-navigation-ex"</span>
<span class="token punctuation">]</span><span class="token punctuation">,</span>
<span class="token property">"pluginsConfig"</span><span class="token operator">:</span> <span class="token punctuation">{</span>
	<span class="token property">"anchor-navigation-ex"</span><span class="token operator">:</span> <span class="token punctuation">{</span>
         <span class="token comment">// 标题是否显示层级序号.页面标题和导航中的标题都会加上层级显示</span>
		<span class="token property">"showLevel"</span><span class="token operator">:</span> <span class="token boolean">false</span><span class="token punctuation">,</span>
		<span class="token property">"isShowTocTitleIcon"</span><span class="token operator">:</span> <span class="token boolean">true</span><span class="token punctuation">,</span>
		<span class="token property">"tocLevel1Icon"</span><span class="token operator">:</span> <span class="token string">"fa fa-hand-o-right"</span><span class="token punctuation">,</span>
		<span class="token property">"tocLevel2Icon"</span><span class="token operator">:</span> <span class="token string">"fa fa-hand-o-right"</span><span class="token punctuation">,</span>
		<span class="token property">"tocLevel3Icon"</span><span class="token operator">:</span> <span class="token string">"fa fa-hand-o-right"</span>
	<span class="token punctuation">}</span>
<span class="token punctuation">}</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br></div></div><p>隐藏元素 <a href="https://blog.csdn.net/weixin_37865166/article/details/91899788?utm_medium=distribute.pc_relevant.none-task-blog-2~default~baidujs_title~default-0.no_search_link&amp;spm=1001.2101.3001.4242" target="_blank" rel="noopener noreferrer">ref<OutboundLink/></a></p>
<div class="language-json ext-json line-numbers-mode"><pre v-pre class="language-json"><code><span class="token property">"plugins"</span><span class="token operator">:</span> <span class="token punctuation">[</span>
    <span class="token string">"hide-element"</span>
<span class="token punctuation">]</span><span class="token punctuation">,</span>
<span class="token property">"pluginsConfig"</span><span class="token operator">:</span> <span class="token punctuation">{</span>
    <span class="token property">"hide-element"</span><span class="token operator">:</span> <span class="token punctuation">{</span>
        <span class="token property">"elements"</span><span class="token operator">:</span> <span class="token punctuation">[</span><span class="token string">".gitbook-link"</span><span class="token punctuation">]</span>
    <span class="token punctuation">}</span>
<span class="token punctuation">}</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br></div></div>